import { ADD_NEW_PRODUCT, INIT_ADD_PRODUCT } from "../actions/actionTypes";

let initialState = {
  products: [],
  prompt: true
};

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case INIT_ADD_PRODUCT:
      return {
        ...state,
        prompt: true
      };
    case ADD_NEW_PRODUCT:
      let newProductArray = [...state.products, action.productInfo];
      return {
        ...state,
        products: newProductArray,
        prompt: false
      };
    default: {
      return state;
    }
  }
};

export default reducer;
