import React from "react";
// import TestImage from "../../shared/images/bag2.jpg";
import classes from "./ImagePreview.module.css";
import BackDrop from "../UI/Backdrop/Backdrop";
import { faTimes } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
const ImagePreview = props => {
  let prevClasses = props.onPrev
    ? [classes.ImagePreview, classes.zoomClass].join(" ")
    : classes.ImagePreview;
  return (
    <div className={classes.ImagePreviewContainer}>
      <BackDrop show={props.onPrev} />
      <div className={prevClasses}>
        <div className={classes.imgCont}>
          <img src={props.onPrev} alt="test" />
        </div>

        {/* <div style={{ position: "absolute", top: "20px", right: "30px" }}>
          
        </div> */}
        <div className={classes.PreviewHeader}>
          <h2>Product Image Preview</h2>
          <FontAwesomeIcon icon={faTimes} size="2x" onClick={props.closed} />
        </div>
      </div>
    </div>
  );
};

export default ImagePreview;
