import React, { Component } from "react";
import Input from "../../../components/UI/Input/Input";
import Button from "../../../components/UI/Button/Button";
import classes from "./BusinessSellerSignUp.module.css";
// import BusinessSeller from "../../../shared/images/businessseller.jpg";
import SellerSignUpLayout from "../../../components/UI/SellerSignUpLayout/SellerSignUpLayout";
import Spinner from "../../../components/UI/Spinner/Spinner";
import { updateObject, checkValidity } from "../../../shared/utitlity";

class BusinessSellerSignUp extends Component {
  state = {
    controls: {
      businessName: {
        elementType: "input",

        elementConfig: {
          id: "businessName",
          type: "Business Name",
          placeholder: "Business Name"
        },

        value: "",

        validation: {
          required: true
        },

        valid: false,
        touched: false,
        blurred: false
      },
      businessRegistrationNumber: {
        elementType: "input",

        elementConfig: {
          id: "businessRegNumber",
          type: "Business Reg Number",
          placeholder: "Business Reg Number"
        },

        value: "",

        validation: {
          required: true,
          isANumber: true
        },

        valid: false,
        touched: false,
        blurred: false
      },
      businessType: {
        elementType: "selectWithLabel",

        elementConfig: {
          id: "businessType",
          type: "Business Type",

          options: [
            { value: "B2B", displayValue: "B2B" },
            { value: "B2C", displayValue: "B2C" }
            // { value: "+422", displayValue: "+422" }
          ]
        },
        value: "",

        validation: {
          required: true
        },

        valid: false,
        touched: false,
        blurred: false
      },
      mainProducts: {
        elementType: "input",

        elementConfig: {
          id: "mainProducts",
          type: "Main Products",
          placeholder: "Seperated Products with a comma (,)"
        },

        value: "",

        validation: {
          required: true
        },

        valid: false,
        touched: false,
        blurred: false
      },

      businessAddressOne: {
        elementType: "input",

        elementConfig: {
          id: "businessAddressOne",
          type: "Business Address 1",
          placeholder: "Business Address 1"
        },

        value: "",

        validation: {
          required: true
        },

        valid: false,
        blurred: false,
        touched: false
      },
      businessAddressTwo: {
        elementType: "input",

        elementConfig: {
          id: "businessAddressTwo",
          type: "Business Address 2",
          placeholder: "Business Address 2"
        },

        value: "",

        validation: {
          required: true
        },

        valid: false,
        blurred: false,
        touched: false
      },
      state: {
        elementType: "selectWithLabel",

        elementConfig: {
          id: "state",
          type: "State",
          placeholder: "Enter your State",

          options: [
            { value: "Lagos", displayValue: "Lagos" },
            { value: "Anambra", displayValue: "Anambra" },
            { value: "Delta", displayValue: "Delta" },
            { value: "Edo", displayValue: "Edo" },
            { value: "FCT", displayValue: "FCT" },
            { value: "Nassarawa", displayValue: "Nassarawa" }
          ]
        },

        value: "",

        validation: {
          required: true
        },

        valid: false,
        blurred: false,
        touched: false
      },
      City: {
        elementType: "selectWithLabel",

        elementConfig: {
          id: "city",
          type: "City",
          placeholder: "Enter your City",

          options: [
            { value: "Yaba", displayValue: "Yaba" },
            { value: "Akoka", displayValue: "Akoka" },
            { value: "Ikeja", displayValue: "Ikeja" }
          ]
        },

        value: "",

        validation: {
          required: true
        },

        valid: false,
        blurred: false,
        touched: false
      },
      password: {
        elementType: "input",

        elementConfig: {
          id: "password",
          type: "password",
          placeholder: "Your Password"
        },

        value: "",

        validation: {
          required: true,
          minLength: 8
        },

        valid: false,
        blurred: false,
        touched: false
      }
    },

    //Here is the personal controls

    personalControls: {
      businessName: {
        elementType: "fileinput",

        elementConfig: {
          id: "businessName",
          type: "Business Name",
          placeholder: "Business Name"
        },

        value: "",

        validation: {
          required: true
        },

        valid: false,
        touched: false,
        blurred: false
      }
    },
    formValidity: false,
    individualSellerSelected: true,
    businessSellerSelected: false,
    documents: {
      businessReg: ""
    }
  };

  onChangeHandler = (event, controlName) => {
    console.log(event.target);
    let updatedControls = updateObject(this.state.controls, {
      ...this.state.controls,
      [controlName]: updateObject(this.state.controls[controlName], {
        value: event.target.value,
        valid: checkValidity(
          event.target.value,
          this.state.controls[controlName].validation
        ),
        touched: true
      })
    });
    if (
      controlName === "phoneNumber" &&
      event.target.id === "phonenumberInputModule"
    ) {
      updatedControls = updateObject(this.state.controls, {
        ...this.state.controls,
        [controlName]: updateObject(this.state.controls[controlName], {
          dropDownValue: event.target.value,

          touched: true
        })
      });
    }
    let isValid = true;
    for (let inputIdentifier in updatedControls) {
      isValid = isValid && updatedControls[inputIdentifier].valid;
    }
    this.setState({ controls: updatedControls, formValidity: isValid });
  };
  onPersonalChangeHandler = (event, controlName) => {
    console.log(event.target);
    let updatedControls = updateObject(this.state.personalControls, {
      ...this.state.personalControls,
      [controlName]: updateObject(this.state.personalControls[controlName], {
        value: event.target.value,
        valid: checkValidity(
          event.target.value,
          this.state.personalControls[controlName].validation
        ),
        touched: true
      })
    });
    if (
      controlName === "phoneNumber" &&
      event.target.id === "phonenumberInputModule"
    ) {
      updatedControls = updateObject(this.state.personalControls, {
        ...this.state.personalControls,
        [controlName]: updateObject(this.state.personalControls[controlName], {
          dropDownValue: event.target.value,

          touched: true
        })
      });
    }
    let isValid = true;
    for (let inputIdentifier in updatedControls) {
      isValid = isValid && updatedControls[inputIdentifier].valid;
    }
    this.setState({ personalControls: updatedControls, formValidity: isValid });
  };

  onSubmitHandler = event => {
    event.preventDefault();
    const { email, password, firstName, lastName } = this.state.controls;
    console.log(email, password, firstName, lastName);
    console.log("props", this.props);
    this.props.history.push("/");
  };

  onBlurHandler = (event, inputIdentifier) => {
    let controlsForBlur = {
      ...this.state.controls,
      [inputIdentifier]: {
        ...this.state.controls[inputIdentifier],
        blurred: this.state.controls[inputIdentifier].value !== null
      }
    };
    this.setState({ controls: controlsForBlur });
  };
  onPersonalBlurHandler = (event, inputIdentifier) => {
    let controlsForBlur = {
      ...this.state.personalControls,
      [inputIdentifier]: {
        ...this.state.personalControls[inputIdentifier],
        blurred: this.state.personalControls[inputIdentifier].value !== null
      }
    };
    this.setState({ personalControls: controlsForBlur });
  };
  onFilePickedHandler = e => {
    const displayValueArray = e.target.value.split("\\");

    console.log(displayValueArray);
    const documents = {
      ...this.state.documents,
      businessReg: displayValueArray[displayValueArray.length - 1]
    };
    this.setState({
      documents
    });
  };

  render() {
    let formElementsArray = [];
    let personalFormElementsArray = [];

    for (let key in this.state.controls) {
      formElementsArray.push({
        id: key,
        config: this.state.controls[key]
      });
    }
    for (let key in this.state.personalControls) {
      personalFormElementsArray.push({
        id: key,
        config: this.state.personalControls[key]
      });
    }
    let form = formElementsArray.map(formElement => (
      <Input
        key={formElement.id}
        elementType={formElement.config.elementType}
        elementConfig={formElement.config.elementConfig}
        value={formElement.config.value}
        changed={event => {
          this.onChangeHandler(event, formElement.id);
        }}
        invalid={!formElement.config.valid}
        shouldBeValidated={true}
        touched={formElement.config.touched}
        valueType={formElement.id}
        blurred={event => this.onBlurHandler(event, formElement.id)}
        isBlurredOut={formElement.config.blurred}
      />
    ));

    if (this.props.loading) {
      form = <Spinner />;
    }

    return (
      <SellerSignUpLayout isSecondLevel subHeader="Business Seller Register">
        <div className={classes.AuthContainer}>
          <div
            className={[classes.BusinessDetailsHeader, classes.Appear].join(
              " "
            )}
          >
            <h2>Business Details</h2>
          </div>
          <div className={[classes.Auth, classes.Appear].join(" ")}>{form}</div>
          <div className={classes.BusinessDetailsHeader}>
            <h2>Upload Business Registration Document</h2>
          </div>
          {/* <div className={classes.Auth}>{personalForm}</div> */}
          <div className={classes.inputfileCont}>
            <input
              type="file"
              name="file"
              id="file"
              onChange={this.onFilePickedHandler}
              className={classes.inputfile}
            />
            <label htmlFor="file">
              {!this.state.documents.businessReg ? (
                <div>
                  Choose a File{" "}
                  <p style={{ color: "white" }}>(* png or jpg *)</p>
                </div>
              ) : (
                <p>file Uploaded: {this.state.documents.businessReg}</p>
              )}
            </label>
          </div>
          <div style={{ textAlign: "center" }}> </div>
        </div>
        <div className={classes.ButtonCont}>
          <Button clicked={this.toBusinessSeller} btnType="Success">
            Next
          </Button>
        </div>
      </SellerSignUpLayout>
    );
  }
}

export default BusinessSellerSignUp;
