import React from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

import classes from "./ProductSummary.module.css";
const ProductSummary = ({ title, quantity, price, icon }) => {
  return (
    <div className={classes.ProductContainer}>
      <div className={classes.Picture} />
      <h6>{title}</h6>
      <h6>{quantity}</h6>
      <div className={classes.PriceAndSettingCont}>
        <h6>&#8358;{price}</h6>
        <div className={[classes.tooltip, classes.desktopOnly].join(" ")}>
          <FontAwesomeIcon icon={icon} size="2x" />
          <div className={classes.options}>
            <p>View Product</p>
            <p>Edit Product</p>
            <p>Promote this Product</p>
            <p>Delete this Product</p>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ProductSummary;
