import React, { Component } from "react";
import SellerBackendLayout from "../../../components/UI/sellerBackendLayout/sellerBackendLayout";
import classes from "./AddProductContainer.module.css";
import Input from "../../../components/UI/Input/Input";
import {
  addNewProduct,
  initAddProduct
} from "../../../store/actions/actionIndex";
import { connect } from "react-redux";
import { Prompt, Link } from "react-router-dom";
import ImageInput from "../../../components/imageInput/imageInput";
import { updateObject, checkValidity } from "../../../shared/utitlity";
import ImagePreview from "../../../components/ImagePreview/ImagePreview";
import renderSub from "./renderSubCategory";
import renderInnerSubCat from "./renderInnerSubCat";

class AddProductContainer extends Component {
  state = {
    controls: {
      productName: {
        elementType: "input",

        elementConfig: {
          id: "name",
          type: "Product Name",
          placeholder: "Enter your product name"
        },

        value: "",

        validation: {
          required: true
        },

        valid: false,
        touched: false,
        blurred: false
      },
      description: {
        elementType: "textarea",

        elementConfig: {
          id: "productdescription",
          type: "Description/Product Speifications",
          placeholder: "Enter Product Description"
        },

        value: "",

        validation: {
          required: true
        },

        valid: false,
        touched: false,
        blurred: false
      },

      category: {
        elementType: "selectWithLabel",

        elementConfig: {
          id: "sellercategory",
          type: "Category",

          options: [
            {
              value: "",
              displayValue: "--Select Category--"
            },
            {
              value: "Agriculture & Food",
              displayValue: "Agriculture & Food"
            },
            {
              value: "Apparel Textiles & Accessories",
              displayValue: "Apparel Textiles & Accessories"
            },
            {
              value: "Auto & Transportation",
              displayValue: "Auto & Transportation"
            },
            {
              value: "Bags, Shoes & Accessories",
              displayValue: "Bags, Shoes & Accessories"
            },
            {
              value: "Electronics",
              displayValue: "Electronics"
            },
            {
              value: "Electrical Equipment, Components & Telecoms",
              displayValue: "Electrical Equipment, Components & Telecoms"
            },
            {
              value: "Gifts Sports & Toys",
              displayValue: "Gifts Sports & Toys"
            },
            {
              value: "Health & Beauty",
              displayValue: "Health & Beauty"
            },
            {
              value: "Home, Lights & Construction",
              displayValue: "Home, Lights & Construction"
            },
            {
              value: "Machinery, Industrial Parts & Tools",
              displayValue: "Machinery, Industrial Parts & Tools"
            },
            {
              value: "Metallurgy, Chemicals, Rubber & Plastics",
              displayValue: "Metallurgy, Chemicals, Rubber & Plastics"
            },
            {
              value: "Packaging & Advertising Services",
              displayValue: "Packaging & Advertising Services"
            }
          ]
        },
        value: "",

        validation: {
          required: true
        },

        valid: false,
        touched: false,
        blurred: false
      },
      subCategory: {
        elementType: "selectWithLabel",

        elementConfig: {
          id: "sellersubcategory",
          type: "Sub Category",

          options: [{ value: "", displayValue: "" }]
        },
        value: "",

        validation: {
          required: true
        },

        valid: false,
        touched: false,
        blurred: false
      },
      subInnerCat: {
        elementType: "selectWithLabel",

        elementConfig: {
          id: "subInnerCat",
          type: "Item type",

          options: [{ value: "", displayValue: "" }]
        },
        value: "",

        validation: {
          required: true
        },

        valid: false,
        touched: false,
        blurred: false
      },
      productQuantity: {
        elementType: "input",

        elementConfig: {
          id: "productQuantity",
          type: "Product Quantity",
          placeholder: "Enter Product Quantity"
        },

        value: "",

        validation: {
          required: true
        },

        valid: false,
        touched: false,
        blurred: false
      },

      price: {
        elementType: "input",

        elementConfig: {
          id: "productprice",
          type: "tel",
          placeholder: "Enter Product Price",
          "data-isnum": "true"
        },

        value: "",

        validation: {
          required: true
        },

        valid: false,
        blurred: false,
        touched: false
      },
      referenceNumber: {
        elementType: "input",

        elementConfig: {
          id: "productserialnumber",
          type: "Product Serial Number",
          placeholder: "Enter Product Serial Number"
        },

        value: "",

        validation: {
          required: true
        },

        valid: false,
        blurred: false,
        touched: false
      }
    },

    formValidity: false,
    preValid: false,
    imagePreviewUrls: {
      fileOne: null,
      fileTwo: null,
      fileThree: null
    },
    files: {
      fileOne: null,
      fileTwo: null,
      fileThree: null
    },
    dragOverStyle: {
      fileOne: null,
      fileTwo: null,
      fileThree: null
    },
    // dragOverCursorStyle: "default",
    isDragOver: {
      fileOne: null,
      fileTwo: null,
      fileThree: null
    },
    prevUrl: null
  };
  preventDefaultFunc = e => {
    e.preventDefault();
  };
  componentWillMount() {
    onbeforeunload = e => "Don't leave";
    window.addEventListener("dragover", this.preventDefaultFunc, false);
    window.addEventListener("drop", this.preventDefaultFunc, false);
  }
  componentDidMount() {
    this.props.onPageLoad();
  }

  // Clear listener
  componentWillUnmount() {
    onbeforeunload = null;
    window.removeEventListener("dragover", this.preventDefaultFunc);
    window.removeEventListener("drop", this.preventDefaultFunc);
  }

  onChangeHandler = (event, controlName) => {
    // console.log(event.target);

    let updatedControls = updateObject(this.state.controls, {
      ...this.state.controls,
      [controlName]: updateObject(this.state.controls[controlName], {
        value: event.target.value,
        valid: checkValidity(
          event.target.value,
          this.state.controls[controlName].validation
        ),
        touched: true
      })
    });
    if (controlName === "price") {
      if (
        !/^[0-9]*$/.test(event.target.value) &&
        this.state.controls[controlName].value.length === 0
      )
        return;
      else {
        console.log(updatedControls[controlName].value);
        let numberValue =
          updatedControls[controlName].value.length === 0
            ? ""
            : parseFloat(updatedControls[controlName].value.replace(/,/g, ""))
                .toFixed(0)
                .toString()
                .replace(/\B(?=(\d{3})+(?!\d))/g, ",");
        console.log(numberValue);
        if (updatedControls[controlName].value.length > 16) {
          return;
        }

        updatedControls = {
          ...updatedControls,
          [controlName]: {
            ...updatedControls[controlName],
            value: numberValue
          }
        };
      }
    }
    if (controlName === "productQuantity") {
      if (!/^[0-9]*$/.test(event.target.value)) return;
    }

    let isValid = true;
    for (let inputIdentifier in updatedControls) {
      isValid = isValid && updatedControls[inputIdentifier].valid;
    }
    if (controlName === "category") {
      // console.log(renderSub(updatedControls["category"].value));
      updatedControls = {
        ...updatedControls,
        subInnerCat: {
          ...updatedControls.subInnerCat,
          elementConfig: {
            ...updatedControls["subInnerCat"].elementConfig,
            options: [{ value: "", displayValue: "" }]
          }
        },
        subCategory: {
          ...updatedControls.subCategory,
          elementConfig: {
            ...updatedControls["subCategory"].elementConfig,
            options: renderSub(updatedControls["category"].value)
          }
        }
      };
    }
    this.setState(pState => {
      return {
        controls: updatedControls
      };
    });
    if (controlName === "subCategory") {
      // console.log(renderInnerSubCat(updatedControls["subCategory"].value));
      updatedControls = {
        ...updatedControls,
        subInnerCat: {
          ...updatedControls.subInnerCat,
          elementConfig: {
            ...updatedControls["subInnerCat"].elementConfig,
            options: renderInnerSubCat(updatedControls["subCategory"].value)
          }
        }
      };
    }
    this.setState(pState => {
      return {
        controls: updatedControls
      };
    });
    // console.log(isValid, "isValid");
    this.setState(prevState => {
      let gval = true;
      if (
        (isValid && prevState.files.fileOne) ||
        (isValid && prevState.files.fileTwo) ||
        (isValid && prevState.files.fileThree)
      ) {
        gval = true;
      } else {
        gval = false;
      }
      if (!isValid) {
        gval = false;
      }

      return {
        controls: updatedControls,
        preValid: isValid,
        formValidity: gval
      };
    });
  };

  onBlurHandler = (event, inputIdentifier) => {
    let controlsForBlur = {
      ...this.state.controls,
      [inputIdentifier]: {
        ...this.state.controls[inputIdentifier],
        blurred: this.state.controls[inputIdentifier].value !== null
      }
    };
    this.setState({ controls: controlsForBlur });
  };
  handleImageChange = (e, imgControl) => {
    // console.log(e);
    e.preventDefault();

    let reader = new FileReader();
    let file = e.target.files[0];
    // console.log(file);
    // let progress = 0;
    reader.onprogress = data => {
      console.log(data);
      if (data.lengthComputable) {
        // var progress = parseInt((data.loaded / data.total) * 100, 10);
        // console.log(progress);
      }
    };
    reader.onloadend = () => {
      this.setState(prevState => {
        return {
          files: {
            ...prevState.files,
            [imgControl]: file
          },
          imagePreviewUrls: {
            ...prevState.imagePreviewUrls,
            [imgControl]: reader.result
          },
          formValidity: prevState.preValid && true
        };
      });
      // console.log(this.state.formValidity);
      // console.log(reader.result);
      // console.log(this.state.files[imgControl], `file for ${imgControl}`);
    };

    if (file && file.type.match("image.*")) {
      reader.readAsDataURL(file);
    } else {
      return;
    }
  };

  onSubmitHandler = event => {
    event.preventDefault();
    const {
      productName,
      description,
      category,
      productQuantity,
      price,
      referenceNumber
    } = this.state.controls;
    const { fileOne, fileTwo, fileThree } = this.state.files;
    console.log(fileOne);
    const productInfo = {
      id: Math.random(),
      productName: productName.value,
      description: description.value,
      category: category.value,
      productQuantity: productQuantity.value,
      price: price.value,
      referenceNumber: referenceNumber.value,
      productFiles: {
        fileOne,
        fileTwo,
        fileThree
      }
    };
    console.log(
      productName.value,
      description.value,
      category.value,
      productQuantity.value,
      price.value,
      referenceNumber.value
    );
    console.log(productInfo);
    this.props.onProdAdd(productInfo);
  };

  dragLeaveHandler = (e, imgControl) => {
    e.preventDefault();
    // console.log(e, "exit");
    this.setState(prevState => {
      return {
        dragOverStyle: {
          ...prevState.dragOverStyle,
          [imgControl]: "1"
        },
        isDragOver: {
          ...prevState.isDragOver,
          [imgControl]: null
        }
      };
    });
    // this.setState({ dragOverStyle: "1", isDragOver: null });
  };

  dragOverHandler = (e, imgControl) => {
    e.preventDefault();
    // console.log(e, "dragOver")
    this.setState(prevState => {
      return {
        dragOverStyle: {
          ...prevState.dragOverStyle,
          [imgControl]: "0.6"
        },
        isDragOver: {
          ...prevState.isDragOver,
          [imgControl]: "true"
        }
      };
    });
  };

  onDropHandler = (e, imgControl) => {
    e.preventDefault();
    e.stopPropagation();
    const customEvent = {
      preventDefault: () => console.log("prevented"),
      target: {
        files: [e.dataTransfer.files[0]]
      }
    };
    this.handleImageChange(customEvent, imgControl);
    this.setState(prevState => {
      return {
        dragOverStyle: {
          ...prevState.dragOverStyle,
          [imgControl]: "1"
        },
        isDragOver: {
          ...prevState.isDragOver,
          [imgControl]: null
        }
      };
    });

    console.log(e.dataTransfer.files);
  };

  closedUrlHandler = () => {
    this.setState({
      prevUrl: null
    });
  };
  showPreview = imgControl => {
    this.setState({ prevUrl: this.state.imagePreviewUrls[imgControl] });
  };

  render() {
    let formElementsArray = [];

    for (let key in this.state.controls) {
      formElementsArray.push({
        id: key,
        config: this.state.controls[key]
      });
    }

    let form = formElementsArray.map(formElement => (
      <Input
        key={formElement.id}
        elementType={formElement.config.elementType}
        elementConfig={formElement.config.elementConfig}
        value={formElement.config.value}
        changed={event => {
          this.onChangeHandler(event, formElement.id);
        }}
        invalid={!formElement.config.valid}
        shouldBeValidated={true}
        touched={formElement.config.touched}
        valueType={formElement.id}
        blurred={event => this.onBlurHandler(event, formElement.id)}
        isBlurredOut={formElement.config.blurred}
      />
    ));
    let { fileOne, fileTwo, fileThree } = this.state.imagePreviewUrls;

    let $imagePreviewOne = null;
    let $imagePreviewTwo = null;
    let $imagePreviewThree = null;
    if (fileOne) {
      $imagePreviewOne = (
        <div className={classes.imgPrevBox}>
          <div className={classes.ChangeImage}>
            <p
              className={classes.ChangeImageText}
              style={{ position: "absolute" }}
              onClick={() => this.showPreview("fileOne")}
            >
              Preview
            </p>
            <label
              className={classes.ChangeImageTextTwo}
              htmlFor="productfileOne"
            >
              Change{" "}
            </label>
          </div>
          <img alt={`${fileOne}`} src={fileOne} />
        </div>
      );
    }
    if (fileTwo) {
      $imagePreviewTwo = (
        <div className={classes.imgPrevBox}>
          <div className={classes.ChangeImage}>
            <p
              className={classes.ChangeImageText}
              style={{ position: "absolute" }}
              onClick={() => this.showPreview("fileTwo")}
            >
              Preview
            </p>
            <label
              className={classes.ChangeImageTextTwo}
              htmlFor="productfileTwo"
            >
              Change{" "}
            </label>
          </div>

          <img alt={`${fileTwo}`} src={fileTwo} />
        </div>
      );
    }
    if (fileThree) {
      $imagePreviewThree = (
        <div className={classes.imgPrevBox}>
          <div className={classes.ChangeImage}>
            <p
              className={classes.ChangeImageText}
              style={{ position: "absolute" }}
              onClick={() => this.showPreview("fileThree")}
            >
              Preview
            </p>
            <label
              className={classes.ChangeImageTextTwo}
              htmlFor="productfileThree"
            >
              Change{" "}
            </label>
          </div>

          <img alt={`${fileThree}`} src={fileThree} />
        </div>
      );
    }
    const imageInputStaticValues = [
      {
        name: "fileOne",
        id: "productfileOne",
        imagepreview: $imagePreviewOne,
        imagenumber: "One",
        isover: this.state.isDragOver.fileOne,
        inputid: "productfileOne"
      },
      {
        name: "fileTwo",
        id: "productfileTwo",
        imagepreview: $imagePreviewTwo,
        imagenumber: "Two",
        isover: this.state.isDragOver.fileTwo,
        inputid: "productfileTwo"
      },
      {
        name: "fileThree",
        id: "productfileThree",
        imagepreview: $imagePreviewThree,
        imagenumber: "Three",
        isover: this.state.isDragOver.fileThree,
        inputid: "productfileThree"
      }
    ];

    const imageInputCard = imageInputStaticValues.map(card => {
      return (
        <div
          className={classes.InputCard}
          key={card.name}
          style={{
            opacity: this.state.dragOverStyle[card.name]
            // cursor: this.state.dragOverCursorStyle
          }}
          onDragLeave={e => {
            this.dragLeaveHandler(e, card.name);
          }}
          onDragOver={e => {
            this.dragOverHandler(e, card.name);
          }}
          onDrop={e => {
            this.onDropHandler(e, card.name);
          }}
        >
          <ImageInput
            onChange={e => this.handleImageChange(e, card.name)}
            name={card.name}
            type="file"
            id={card.id}
            imagepreview={card.imagepreview}
            imagenumber={card.imagenumber}
            isover={this.state.isDragOver[card.name]}
            inputid={card.inputid}
          />
        </div>
      );
    });

    return (
      <SellerBackendLayout>
        {/* {!this.props.prompt ? <Redirect to="/sellerpage/products" /> : null} */}
        <Prompt
          when={this.props.prompt}
          message="You may have unsaved changes, are you sure you want to leave?"
        />
        <div className={classes.prevCont}>
          <ImagePreview
            onPrev={this.state.prevUrl}
            closed={this.closedUrlHandler}
          />
        </div>
        <div className={classes.AddProductContainer}>
          <h2>Add a new product</h2>
          <div className={classes.FormContainer}>
            {form}
            <div>
              <h2>Upload Product Images</h2>
            </div>
            <div className={classes.ImageUploadContainer}>{imageInputCard}</div>
            <button
              disabled={!this.state.formValidity}
              onClick={this.onSubmitHandler}
            >
              ADD PRODUCT
            </button>
            {/* <div>{JSON.stringify(this.props.prodState)}</div> */}
            {!this.props.prompt ? (
              <div className={classes.Success}>
                <p>Product Successfully Added</p>
                <Link to="/sellerpage/products">Back To Product Page</Link>
              </div>
            ) : null}
          </div>
        </div>
      </SellerBackendLayout>
    );
  }
}
const mapStateToProps = state => {
  return {
    prodState: state.prod.products,
    prompt: state.prod.prompt
  };
};
const mapDispatchToProps = dispatch => {
  return {
    onProdAdd: prodInfo => dispatch(addNewProduct(prodInfo)),
    onPageLoad: () => dispatch(initAddProduct())
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(AddProductContainer);
